body {
    columns: #8f8f8f;
    font:12px Tahoma, sans-serif;
    background-color: #f8f8f8;
    border-top: 5px solid #e7e7e7;
    margin:0;
}
input[type="text"] {
    background-color: #f3f3f3;
    border: 1px solid #e7e7e7;
    height:30px;
    color: #b2b2b2;
    padding: 0 10px;
    vertical-align: top;
}
button {
    color: #fff;
    background-color: #29c5eb;
    border: none;
    height: 32px;
    font-family: 'Oswald', sans-serif;
}
p {
    margin:20px 0;
}
.wrapper {
    max-width: 960px;
    margin: auto;
}
header {
    padding:20px 0;
}
form[name=search] {
    float:right;
}
nav a {
    text-decoration:none;
}
nav ul {
    margin:0;
    padding: 0;
}
nav li {
    list-style-position: inside;
    font: 14px 'Oswald', sans-serif;
    padding:10px;
}
.top-menu li {
    display:inline-block;
    padding:10px 30px;
    margin:0;
}
.top-menu li.active {
    background:#29c5eb;
    color:#fff;
}
.top-menu a {
    color:#b2b2b2;
}
.heading {
    /* background: transparent url(../images/...........); */
    margin:30px 0;
    padding-left: 20px;
}
h1 {
    display: inline-block;
    color: #7e7e7e;
    font: 40px/40px 'Oswald', sans-serif;
    /* background: url(../images/.....); */
    margin:0;
    padding:0 10px;
}
aside {
    float:left;
    width: 250px;
}
section {
    margin-left:280px;
    padding-bottom: 50px;
}
.aside-menu li {
    font-weight: 300;
    list-style-type: square;
    border-top: 1 px solid #e7e7e7;
}
.aside-menu li:first-child {
    border:none;
}
.aside-menu li.active {
    color:#29c5eb
}
.aside-menu a {
    color:#8f8f8f
}
aside > h2 {
    background: #29c5eb;
    font: 14px 'Oswald', san-serif;
    color:#fff;
    padding:10px;
    margin:30px 0 0 0;
}
aside > p {
    background: #f3f3f3;
    border: 1px solid #e7e7e7;
    padding: 10px;
    margin: 0;
}
blockquote {
    margin:0;
    background: #29c5eb;
    padding:10px 20px;
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
}
blockquote p {
    color: #fff;
    font-style: italic;
    font-size: 33px;
    margin:0;
}
blockquote cite {
    display: block;
    font-size: 20px;
    font-style:normal;
    color: #1d8eab;
    margin: 0;
    text-align: right;
}
